.checkbox {
  --width: 40px;
  --height: 20px;
  gap: 15px;
  display: flex;
}

.checkbox-box {
  width: var(--width);
  height: var(--height);
  border-radius: var(--width);
  border: 1px solid #fd5103;
  transition: all .1s;
  position: relative;
}

.checkbox-input {
  display: none;
}

.checkbox-input:checked ~ .checkbox-box {
  background-color: #fd5103;
  border: 1px solid #fcfcfc;
}

.checkbox-input:checked ~ .checkbox-box:before {
  background-color: #fcfcfc;
  left: 21px;
}

.checkbox-input:checked ~ .darkk {
  color: #fd5103;
}

.checkbox-input:checked ~ .ligth {
  color: #fcfcfc;
}

.checkbox-box:before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #fd5103;
  border-radius: 50%;
  transition: all .1s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.dark {
  background-color: #202025;
}

.no-scroll {
  overflow: hidden;
}

